import React, { useEffect } from "react";
import clsx from "clsx";
import { useDispatch } from "react-redux";
import { Button } from "@material-ui/core";
import {
  useMobile,
  MobileBackgroundCalcIcon,
  DesktopBackgroundCalcIcon,
  MobileSaveEcalcIcon,
  DesktopSaveEcalcIcon,
} from "../../themes";
import { useStyles } from "./SaveEcalcSuccess.styles";
import { Navigation } from "../../lib";
import encodeQueryData from "../common/encodeQueryData";
import { parse } from "query-string";
import { clearEcalc } from "../../actions/eCalcForms";

export default function CalculatorContainer() {
  const isMobile = useMobile();
  const classes = useStyles({ isMobile });
  const dispatch = useDispatch();

  const { deal_id, deal_name, type, hash_id } = parse(window.location.search);

  useEffect(() => {
    if (!hash_id) {
      dispatch(clearEcalc(true));
    }
    dispatch(clearEcalc(false));
  }, [hash_id, dispatch]);

  return (
    <div className={clsx(classes.cssReset, classes.container)}>
      {isMobile ? (
        <MobileBackgroundCalcIcon className={classes.calculatorIcon} />
      ) : (
        <DesktopBackgroundCalcIcon className={classes.calculatorIcon} />
      )}
      <div className={classes.content}>
        <div>
          <p className={classes.title}>Calculator saved to</p>
          <p className={classes.propertyName}>{deal_name}</p>
        </div>
        {isMobile ? <MobileSaveEcalcIcon /> : <DesktopSaveEcalcIcon />}
        <div>
          <Button
            className={classes.goToSavedCalcBtn}
            onClick={() =>
              Navigation.go(
                `/properties/${type}/${deal_id}/ecalc?${encodeQueryData({
                  previous_page: `/e-calc/calculator`,
                })}`
              )
            }
          >
            Go To Saved Calculator
          </Button>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              marginTop: isMobile ? 19 : 31,
              marginBottom: isMobile ? 49 : 91,
            }}
          >
            <span>or</span>
            <div
              className={classes.newCalcBtn}
              onClick={() => Navigation.go(`/e-calc/calculator`)}
            >
              Start a New Calculator
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
